div\r\n                                    style=\"\r\n                                        flex: 1;\r\n                                        display: flex;\r\n                                        justify-content: flex-end;\r\n                                    \"\r\n                                >\r\n                                    <label for=\"seating\">\r\n                                        <span style=\"margin-bottom: 15px\"\r\n                                            >Seating</span\r\n                                        >\r\n                                        <input\r\n                                            type=\"text\"\r\n                                            id=\"seating\"\r\n                                            name=\"poolseating\"\r\n                                            disabled\r\n                                            required\r\n                                            v-model=\"item.Seating\"\r\n                                        />\r\n                                        <span\r\n                                            v-if=\"\r\n                                                errors &&\r\n                                                errors[\r\n                                                    'data' +\r\n                                                        '.' +\r\n                                                        index +\r\n                                                        '.' +\r\n                                                        'Seating'\r\n                                                ]\r\n                                            \"\r\n                                            class=\"errMsg\"\r\n                                            >{{\r\n                                                errors[\r\n                                                    \"data\" +\r\n                                                        \".\" +\r\n  