\n                                            <video\r\n                                                height=\"100%\"\r\n                                                :src=\"item\"\r\n                                                style=\"margin: 5px\"\r\n                                                controls\r\n                                            >\r\n                                                <source\r\n                                                    src=\"movie.mp4\"\r\n                                                    type=\"video/mp4\"\r\n                                                />\r\n                                            </video>\r\n                                            <span\r\n                                                style=\"width: 100%\"\r\n                                                v-if=\"\r\n                                                    errors &&\r\n                                                    errors[\r\n                                                        'data.' +\r\n                                                            index +\r\n                                                            '.uploadvid.' +\r\n                                                            indexx\r\n                                                    ]\r\n                                                \"\r\n                                                class=\"errMsg\"\r\n                                                >{{\r\n                                                    errors[\r\n                                                        \"data.\" +\r\n                                                            index +\r\n                                                            \".uploadvid.\" +\r\n                                                            indexx\r\n                                                    ][0]\r\n                                                }}</span\r\n  